@import "main";
// End Of Import

// Preloader

$base-duration: 500ms;

// Colors
$color-1: #2c3e50;
$color-2: #3498db;
$color-3: #2ecc71;

// Breakpoints
$sm: new-breakpoint(min-width 320px);
$med: new-breakpoint(min-width 768px);
$lg: new-breakpoint(min-width 1024px);

.preloader{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #FFF;
  z-index: 99999;
  @include Flex;
}

.loader {
	position: relative;
	display: grid;
	grid-template-columns: 33% 33% 33%;
	grid-gap: 2px;
	width: 100px;
  height: 100px;
	
	> div {
		position: relative;
		width: 100%;
		height: 100%;
		background: $color-prim;
		transform: scale(0.0);
		transform-origin: center center;
		animation: loader 1s infinite linear;
				
		&:nth-of-type(1),
		&:nth-of-type(5), 
		&:nth-of-type(9) {
			animation-delay: 0.4s;
		}
		
		&:nth-of-type(4),
		&:nth-of-type(8) {
			animation-delay: 0.2s;
		}
		
		&:nth-of-type(2),
		&:nth-of-type(6) {
			animation-delay: 0.6s;
		}
		
		&:nth-of-type(3) {
			animation-delay: 0.8s;
		}
	}
}

@keyframes loader {
	0%   { opacity: 0 }
	40%  { transform: scale(1.0);
		   opacity: 1 }
	80%  { transform: scale(1.0); }
	100% { transform: scale(0.0); }
}

// Preloader


.navover {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba($color-sec, 0.8);
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;

  &.open {
    opacity: 1;
    visibility: visible;
  }
}

.sideNav {
  padding: 24px;
  position: fixed;
  top: 0;
  right: -100%;
  html[dir="ltr"] & {
    left: -100%;
    right: unset;
  }
  width: 260px;
  height: 100%;
  background-color: $color-white;
  z-index: 9999;
  overflow-x: hidden;
  overflow-y: hidden;
  transition: 0.5s;

  &:after{
    z-index: -1;
    content: "";
    width: 260px;
    height: 300px;
    background-color: $color-sec;
    position: absolute;
    bottom: -200px;
    left: 65px;
    transform: rotate(45deg);
  }

  &:before{
    z-index: -1;
    content: "";
    width: 260px;
    height: 300px;
    background-color: $color-sec;
    position: absolute;
    top: -170px;
    left: 30px;
    transform: rotate(45deg) translateX(-50%);
  }

  &.open {
    right: 0;

    html[dir="ltr"] & {
      left: 0;
      right: unset;
    }
  }

  .logo{
    position: absolute;
    bottom: 0;
    right: 20px;
    // html[dir="ltr"] & {
    //   left: 11px;
    //   right: unset;
    // }
    width: 80px;
    /* mix-blend-mode: multiply; */
    opacity: 0.3;
  }

  .opt {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 40px;
    padding: 0 15px 0 0;
    
    html[dir="ltr"] & {
      padding: 0 0 0 15px;
    }

    .lang {
      a {
        color: $color-sec;
        font-size: 15px;

        html[dir="ltr"] & {
          color: $color-prim;
        }
      }
    }

    .close1 {
      font-size: 20px;
      color: $color-prim;
      cursor: pointer;
      display: flex;
      align-items: center;

      html[dir="ltr"] & {
        color: $color-sec;
      }
    }
  }

  .links {
    display: block;

    .list {
      display: flex;
      align-items: center;
      flex-direction: column;
      height: 100%;
      overflow: auto;

      & > li {
        padding: 7px 15px;
        width: 100%;

        a {
          width: 100%;
          display: block;
          font-size: 16px;
          color: $color-sec;
          // line-height: 1.6;
          transition: 0.5s;

          &.lnr {
            font-family: "sa";
            position: relative;
            &:before {
              position: absolute;
              top: 50%;
              left: -12px;
              transform: translateY(-15%);
              font-family: "Linearicons-Free" !important;
              color: $color-sec;
              font-size: 8px;
              transition: 0.5s;
              html[dir="ltr"] & {
                right: -12px;
                left: unset;
              }
            }

            + .dropDown{
              display: none;
              padding-top: 10px;

              li{
                &:not(:last-child){
                  margin-bottom: 10px;
                }

                a{
                  color: #898989;
                }
              }
            }
          }
        }

        &:not(:last-child) {
          border-bottom: 1px solid #dedede;
        }
      }
    }
  }
}

// Side Nav

.searchh {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.95);
  @include Flex;
  z-index: 9999;
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;

  &:after {
    content: "";
    width: 100%;
    height: 30%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    border: 3px solid $color-prim;
    border-right: 0;
    border-left: 0;
    transition: 0.5s 0.5s;
  }

  &:before {
    content: "";
    width: 30%;
    height: 100%;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 0;
    border: 3px solid $color-prim;
    border-top: 0;
    border-bottom: 0;
    transition: 0.5s 0.5s;
  }

  form {
    position: relative;
    width: 50%;
    border: 2px solid $color-prim;
    z-index: 9;
    transform: scale(1.4);
    opacity: 0;
    visibility: hidden;
    transition: 0.5s 0.5s;

    @include Xs {
      width: 90%;
      transform: scale(1.4) translateY(-115px);
    }

    > span {
      position: absolute;
      top: -140px;
      left: 50%;
      transform: translateX(-50%);
      color: #fff;
      font-size: 25px;

      @include Xs {
        top: -90px;
      }
    }

    input {
      width: 100%;
      height: 50px;
      border: 0;
      padding: 0 25px;
      background-color: rgba(255, 255, 255, 0.9);
    }

    button {
      position: absolute;
      top: 0;
      left: 0;
      html[dir="ltr"] & {
        right: 0;
        left: unset;
      }
      height: 100%;
      width: 60px;
      background-color: $color-prim;
      border: 0;
      color: #fff;
      font-size: 25px;
      cursor: pointer;
      transition: 0.5s;

      &:hover {
        background-color: rgba($color-prim, 0.8);
      }
    }
  }

  &.open {
    opacity: 1;
    visibility: visible;

    form {
      opacity: 1;
      visibility: visible;
      transform: scale(1) translateY(-115px);
    }

    &:after {
      height: 100%;
    }

    &:before {
      width: 100%;
    }
  }
}

// Start Header
header {
  height: 100vh;
  position: relative;
  overflow: hidden;

  > img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
  }

  .logo {
    img {
      @include Md {
        height: 100px;
      }

      @include Lg {
        height: auto;
      }

      @include mob375 {
        height: 85px;
      }
    }

    .menuTriger,
    .searchTriger {
      display: none;

      span {
        font-size: 25px;
        color: $color-white;
      }

      @include Sm {
        display: flex;
        position: absolute;
        top: 50%;
        left: 0;
        html[dir="ltr"] & {
          left: unset;
          right: 0;
        }
      }
    }

    .menuTriger {
      span {
        font-size: 28px;
      }
    }

    .searchTriger {
      @include Sm {
        left: 40px;
        html[dir="ltr"] & {
          left: unset;
          right: 40px;
        }
        margin-top: 2px;
      }
    }

    @include Sm {
      width: 100%;
      position: relative;
    }
  }

  nav {
    padding: 10px 0;

    > .container {
      position: relative;
    }

    .lang {
      position: absolute;
      top: 5px;
      left: 0;
      html[dir="ltr"] & {
        left: unset;
        right: 0;
      }
      font-size: 14px;
      color: #c3c3c3;
      font-family: "sa";
      line-height: 1;
      transition: 0.5s;

      @include Sm {
        display: none;
      }

      &:hover {
        color: $color-white;
      }
    }

    .content {
      @include Flex;
      justify-content: space-between;

      .links {
        margin-top: 35px;

        @include Sm {
          display: none;
        }

        ul {
          @include Flex;

          li {
            position: relative;
            &:not(:last-child) {
              margin-left: 30px;
              margin-top: -5px;
              html[dir="ltr"] & {
                margin-left: 0;
                margin-right: 30px;
              }
            }

            a {
              font-size: 18px;
              color: #fff;
              font-family: "sa";
              transition: 0.5s;

              &.lnr {
                &:before {
                  position: absolute;
                  top: 50%;
                  left: -12px;
                  transform: translateY(-15%);
                  font-family: "Linearicons-Free" !important;
                  color: #fff;
                  font-size: 8px;
                  transition: 0.5s;
                  html[dir="ltr"] & {
                    right: -12px;
                    left: unset;
                  }
                }
              }

              &:hover {
                color: $color-prim;

                &:before {
                  color: $color-prim;
                }
              }
            }

            .dropDown {
              position: absolute;
              right: calc(-1 * (160px / 2) + 30px);
              top: 140%;
              width: 160px;
              background-color: rgba($color-white, 0.95);
              padding: 7px 10px;
              opacity: 0;
              visibility: hidden;
              z-index: 9;
              display: flex;
              flex-direction: column;
              transform: translateY(-10px);
              border-radius: 4px;
              transition: 0.5s;

              html[dir="ltr"] & {
                left: calc(-1 * (170px / 2) + 40px);
                right: unset;
                width: 170px;
              }

              &:after {
                content: "";
                width: 0;
                height: 0;
                display: block;
                border-width: 6px;
                border-color: transparent transparent $color-white;
                border-style: solid;
                bottom: 100%;
                position: absolute;
                right: 50%;
                transform: translateX(50%);
              }

              li {
                margin: 0 !important;
                width: 100%;

                &:not(:last-child) {
                  border-bottom: 1px solid #e6e6e6;
                }

                a {
                  line-height: 2.4;
                  font-size: 15px;
                  font-family: "sa";
                  text-align: center;
                  display: block;
                  color: $color-sec;
                  width: 100%;

                  &:hover {
                    color: $color-prim;
                  }

                  html[dir="ltr"] & {
                    font-size: 14px;
                  }

                  &:after {
                    display: none;
                  }
                }
              }
            }

            &:hover {
              .dropDown {
                transform: translateY(0);
                opacity: 1;
                visibility: visible;
              }
            }

            span.lnr-magnifier {
              cursor: pointer;
              font-size: 24px;
              color: #fff;
              margin-right: 20px;
              html[dir="ltr"] & {
                margin-right: 0;
                margin-left: 20px;
              }
            }
          }
        }
      }
    }
  }

  > .content {
    position: absolute;
    top: 50%;
    transform: translateY(-30%);
    right: 0;
    width: 100%;

    p {
      font-size: 24px;
      font-family: "sa";
      color: #fff;

      @include Sm {
        text-align: center;
      }
    }

    h2 {
      width: 61%;
      font-size: 55px;
      font-family: "saBold";
      color: #fff;
      line-height: 1.182;
      margin-bottom: 55px;

      @include Sm {
        width: 100%;
        text-align: center;
        font-size: 40px;
      }
    }

    a {
      @include Flex;
      width: 200px;
      height: 55px;
      background-color: #c8ac66;
      font-size: 16px;
      font-family: "sa";
      color: #fff;
      transition: 0.5s;

      @include Sm {
        margin: auto;
      }

      span {
        font-size: 18px;
        color: currentColor;
        margin-right: 14px;
        margin-top: 3px;
        html[dir="ltr"] & {
          margin-right: 0;
          margin-left: 14px;
        }
      }

      &:hover {
        transform: translateY(3px);
      }
    }
  }

  .social {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 60px;
    html[dir="ltr"] & {
      left: unset;
      right: 60px;
    }
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @include Sm {
      display: none;
    }

    .shape {
      height: 85px;
      width: 1px;
      background-color: #b6b6b7;
    }

    a {
      margin-top: 25px;

      i {
        color: rgba($color-white, 0.55);
        font-size: 18px;
        transition: 0.5s;

        &:hover {
          color: #fff;
        }
      }
    }
  }
}
// End Header

// info section
.info-section {
  padding: 125px 0;
  @media (max-width: 991px) {
