      @media (max-width: 1024px) {
        display: inline;
      }
    }
  }
  .count-container {
    display: flex;
    margin-top: 54px;

    @include Sm{
      justify-content: center;
    }

    @media (max-width: 480px) {
      flex-direction: column;
    }

    .count-box {
      &:last-of-type {
