        }
      }
      h3 {
        margin: 0;
        font-family: "sa";
        font-size: 18px;
        color: rgb(64, 64, 66);
        margin-bottom: -15px;
      }
      .count {
        margin: 0;
        font-family: "saBold";
        font-size: 71.52px;
        color: rgb(201, 173, 101);
      }
    }
  }
}

