// Projects

.projects {
  padding: 100px 0 130px;
  background-color: #f6f6f6;

  @include Sm {
    padding: 40px 0;
  }

  .head {
    @include Flex;
    justify-content: space-between;
    margin-bottom: 40px;

    p {
      margin: 0;
      font-size: 30px;
      font-family: "saMd";
      color: $color-sec;

      @include mob375 {
        font-size: 20px;
      }
    }

    a {
      font-size: 20px;
      color: $color-sec;
    }
  }

  .projectss {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;

    .project {
      width: calc((100% - 60px) / 3);
      position: relative;

      @include Sm {
        width: 100%;
      }

      .overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(#c8ac66, 0.9);
        @include Flex;
        opacity: 0;
        visibility: hidden;
        transition: 0.5s;

        a {
          @include Flex;
          width: 135px;
          height: 40px;
          border: 1px solid #fff;
          font-size: 16px;
          font-family: "sa";
          color: #fff;
          transition: 0.5s 0.3s;
          transform: scale(.8) rotate(-7deg);
          opacity: 0;

          &:hover {
            background-color: #fff;
            color: $color-sec;
          }
        }
      }

      &:hover {
        .overlay {
          opacity: 1;
          visibility: visible;

          a {
            opacity: 1;
            transform: scale(1) rotate(0);
          }
        }
      }

      &:nth-child(4),
      &:nth-child(6) {
        margin-top: -111px;
      }

      &:nth-child(5) {
        margin-top: 34px;
      }

      &:nth-child(7),
      &:nth-child(8) {
        margin-top: -256px;
      }
    }
  }

  .owl-stage {
    display: flex;
    align-items: center;
  }

  .owl-dots {
    width: 100%;
    @include Flex;
    margin-top: 50px;

    button {
      &:not(:last-child) {
        margin-left: 5px;
      }

      span {
        display: flex;
        width: 8px;
        height: 8px;
        background-color: $color-sec;
        border-radius: 50%;
      }

      &.active {
        span {
          background-color: $color-prim;
        }
      }
    }
  }
}

