// footer

footer {
  padding: 65px 0 50px;
  background-image: url("../images/footer-bg.jpg");
  background-size: cover;

  @include Sm {
    padding: 40px 0;

    background-size: 100%;
    background-repeat: no-repeat;
    background-color: #404042;
  }

  .contact{
    padding: 0 0 70px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid rgba(#FFF, .1);
    margin-bottom: 70px;

    @include Sm{
      flex-direction: column;
      align-items: flex-start;
      position: relative;
      z-index: 9;
      margin-bottom: 10px;
      padding: 0 0 20px;
    }

    > div {
      display: flex;
      align-items: center;

      @include mob480{
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
      }

      &.right{
        margin-left: 110px;
        html[dir="ltr"] & {
          margin-left: 0;
          margin-right: 110px;
        }

        @include Sm{
          margin: 0;
          margin-bottom: 25px;
        }
      }
    }

    .icon{
      width: 80px;
      height: 80px;
      border-radius: 50%;
      background-color: rgba(#C8AC66, .2);
      @include Flex;
      margin-left: 23px;
      html[dir="ltr"] & {
        margin-right: 23px;
        margin-left: 0;
      }

      @include mob480{
        margin: 0;
        margin-bottom: 10px;
      }

      span{
        font-size: 40px;
        color: $color-prim;

        &.lnr-envelope{
          transform: translateY(-4px);
        }
      }
    }

    .details{
      p{
        font-size: 18px;
        color: #FFF;
      }

      a{
        font-size: 30px;
        color: #FFF;
        font-family: "saBold";

        @include mob480{
          font-size: 20px;
        }
      }

      @include mob480{
        justify-content: center;
        text-align: center;
      }
    }
  }

  .foot-links {
    // @include Lg {
    //   text-align: right;
    // }

    @include Sm {
      margin-bottom: 15px;
    }

    .collapse {
      display: block;

      @include Sm {
        display: none;
      }

      &.in {
        display: block;
      }
    }

    & > button {
      font-size: 18px;
      font-family: "saBold";
      color: #fff;
      margin-bottom: 35px;
      background-color: transparent;
      border: 0;
      padding: 0;
      pointer-events: none;

      &.trans {
        &:after {
          content: "\2212";
        }
      }

      @include Sm {
        width: 100%;
        margin: auto;
        pointer-events: all;
        margin-bottom: 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
      }

      @include mob480 {
        width: 100%;
      }

      &:after {
        content: "\002B";
        margin-left: 5px;
        font-size: 18px;
        float: left;
        display: none;

        @include Sm {
          display: block;
        }
      }
    }

    ul {
      font-size: 0;

      @include Sm {
        padding-top: 10px;
        padding-right: 10px;
      }

      li {
        &:not(:last-child) {
          margin-bottom: 11px;
        }

        width: 50%;
        display: inline-block;
      }
    }

    a {
      font-size: 16px;
      font-family: "sa";
      color: #c7c7c7;
      transition: 0.5s;

      &:hover {
        color: $color-white;
      }
    }
  }

  .newsletter {
    @include Md {
      margin-top: 35px;
    }

    @include Lg {
      margin-top: 0;
    }

    @include Sm {
      margin-top: 35px;
      text-align: center;
    }

    & > p {
      font-size: 18px;
      font-family: "saBold";
      color: #fff;
      margin-bottom: 35px;
    }

    & > span {
      font-size: 16px;
      font-family: "sa";
      color: #fff;
      margin-bottom: 23px;
      display: block;
    }
  }

  .frm {
    position: relative;
    margin-bottom: 17px;
    width: 360px;

    @include Md {
      margin: auto;
      width: 100%;
    }

    @include Lg{
      width: 360px;
    }

    @include Sm {
      margin: auto;
    }

    @include mob375 {
      width: 280px;
    }

    input {
      padding: 0 20px;
      height: 45px;
      width: 100%;
      border: 0;
      color: #a8a8a8;
      font-size: 14px;
      background-color: #FFF;
      transition: 0.5s;

      &::placeholder {
        color: #a8a8a8;
      }
    }

    button {
      position: absolute;
      top: 50%;
      left: 0px;
      html[dir="ltr"] & {
        left: unset;
        right: 0px;
      }
      transform: translateY(-50%);
      width: 45px;
      height: 45px;
      background-color: #C8AC66;
      border: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      border-top: 1px solid #c8ac66;
      transition: .5s;

      span{
        font-size: 16px;
        color: #FFF;
      }
    }
  }

  .socialP{
    margin-bottom: 35px;
    font-size: 18px;
    font-family: "saBold";
    color: #FFF;

    @include Sm{
      text-align: center;
      margin-top: 30px;
    }
  }

  .social {
    @include Flex;
    justify-content: unset;

    @include Sm{
      justify-content: center;
    }

    a {
      @include Flex;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background-color: rgba($color-white, 0.1);
      font-size: 18px;
      transition: 0.5s;

      &:not(:last-child) {
        margin-left: 5px;

        html[dir="ltr"] & {
          margin-left: 0;
          margin-right: 5px;
        }
      }

      i{
        color: #FFF;
      }

      &:hover {
        background-color: #C8AC66;
        i{
          color: #0b0b0b;
        }
      }
    }
  }
}

// End Of Footer

// End

.end {
  background-color: #404042;
  padding: 0 0 45px;

  @include Sm{
    padding: 0 0 20px;
  }

  .content {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include mob600 {
      flex-direction: column;
    }

    @include Xs {
      text-align: center;
    }

    p,
    a {
      font-size: 16px;
      color: #898989;
      margin-bottom: 0;

      @include Xs {
        font-size: 13px;
      }

      @include mob600 {
        margin-bottom: 10px;
      }
    }

    p {
      @include mob480 {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin-bottom: 0;
      }
    }

    a {
      img {
        width: 58px;
        // height: 21px;
        backface-visibility: hidden;
        transform: translateZ(0);
        margin-right: 5px;
        html[dir="ltr"] & {
          margin-right: 0;
          margin-left: 5px;
        }
      }

      @include mob480 {
        margin-top: 10px;
      }
    }

    > img{
      width: 220px;
      // height: 44px;
      backface-visibility: hidden;
      transform: translateZ(0);
    }
  }
}

